import React from 'react'
import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom';
import Tema2 from './Tema2';
import Tema3 from './Tema3';
import Tema4 from './Tema4';
import Tema5 from './Tema5';
import Tema6 from './Tema6';
import Inicio from './Inicio';

const Tema1 = () =>{
    return (
        <Router>

        <div>
            <div className="menus">
        <h1>3.1 Introduccion a redes WAN</h1>
        <ul className="menu">
            <li><a href="/Inicio">Menu principal</a></li>
            <li><a href="/Tema2">3.2 Elaces: dedicados y públicos</a></li>
            <li><a href="/Tema3">3.3 Topologias WAN</a></li>
            <li><a href="/Tema4">3.4 Tecnologías WAN: PPP, XDLS, frame relay, ISDN, ATM</a></li>
            <li><a href="/Tema5">3.5 Configuración de dispotitivos WAN</a></li>
            <li><a href="/Tema6">3.6 niveles de seguridad en redes VPN</a></li>
        </ul>
        </div>
        <div className="contenido">
            ¿Qué es WAN?<br/><br/>
            Una WAN es una red de comunicación de datos que opera más allá del alcance geográfico
            de una LAN. Las WAN utilizan instalaciones suministradas por un proveedor de servicios, o
            portadora, como una empresa proveedora de servicios de telefonía o una empresa
            proveedora de servicios de cable, para conectar los sitios de una organización entre sí con
            sitios de otras organizaciones, con servicios externos y con usuarios remotos.
            Las tres características principales de las WAN son las siguientes:
            Las WAN generalmente conectan dispositivos que están separados por un área geográfica
            más extensa que la que puede cubrir una LAN.
            Las WAN utilizan los servicios de operadoras, como empresas proveedoras de servicios de
            telefonía, empresas proveedoras de servicios de cable, sistemas satelitales y proveedores
            de servicios de red.
            Las WAN usan conexiones seriales de diversos tipos para brindar acceso al ancho de
            banda a través de áreas geográficas extensas.<br/><br/><br/>
            Descripción General de la Tecnología WAN<br/><br/>
            Los estándares de acceso WAN normalmente describen tanto los métodos de entrega de la
            capa física como los requisitos de la capa de enlace de datos, incluyendo la dirección
            física, el control del flujo y la encapsulación. La definición y la administración de los
            estándares de acceso WAN están a cargo de varias autoridades reconocidas, entre ellas la
            OIE, la TIA, y EIA.
            <img src="https://ccnadesdecero.es/wp-content/uploads/2018/01/WAN-opera-en-capa-1-y-2.png" alt=""/><br/><br/><br/>
            Conceptos de la capa fisica de la Tecnología WAN<br/><br/>
            La capa física de acceso a la WAN describe la conexión física entre la red de la empresa y
            la red del proveedor de servicios. Para describir las conexiones físicas de la WAN, se
            utilizan conceptos como:<br/>
            • Equipo local del cliente (CPE, Customer Premises Equipment): dispositivos y
            cableado interno localizados en las instalaciones del suscriptor y conectados con un
            canal de telecomunicaciones de una portadora. En este contexto, un suscriptor es
            una empresa que contrata los servicios WAN de un proveedor de servicios u
            operadora.<br/>
            • Equipo de comunicación de datos (DCE, Data Communications Equipment):
            también llamado equipo de terminación de circuito de datos, el DCE está
            compuesto por dispositivos que ponen datos en el bucle local. La tarea principal del
            DCE es suministrar una interfaz para conectar suscriptores a un enlace de
            comunicación en la nube WAN.<br/>
            • Equipo terminal de datos (DTE, Data Terminal Equipment): dispositivos del
            cliente que pasan los datos de la red o la computadora host de un cliente para
            transmisión a través de la WAN. El DTE se conecta al bucle local a través del DCE.<br/>
            • Punto de demarcación: punto establecido en un edificio o un complejo para
            separar los equipos del cliente de los equipos del proveedor de servicios.
            Físicamente, el punto de demarcación es la caja de empalme del cableado que se
            encuentra en las instalaciones del cliente y que conecta los cables del CPE con el
            bucle local. <br/><br/><br/>
            Dispositivos WAN<br/><br/>
            Las WAN utilizan numerosos tipos de dispositivos que son específicos para los entornos
            WAN, entre ellos:<br/>
            • Módem: modula una señal portadora analógica para codificar información digital y
            demodula la señal portadora para decodificar la información transmitida. s módems
            más rápidos, por ejemplo los módems por cable y los módems DSL, transmiten
            mediante el uso de frecuencias de banda ancha mayores.<br/>
            • CSU/DSU: las líneas digitales, por ejemplo las líneas portadoras T1 o T3, necesitan
            una unidad de servicio de canal (CSU, channel service unit) y una unidad de servicio
            de datos (DSU, data service unit). La CSU proporciona la terminación para la señal
            digital y garantiza la integridad de la conexión mediante la corrección de errores y
            la supervisión de la línea. La DSU convierte las tramas de la línea Portadora T en
            tramas que la LAN puede interpretar y viceversa.<br/>
            • Servidor de acceso: concentra las comunicaciones de usuarios de servicios de
            acceso con marcación.<br/>
            • Switch WAN: dispositivo de internetworking de varios puertos que se utiliza en
            redes portadoras. Estos dispositivos normalmente conmutan el tráfico, por ejemplo
            Frame Relay, ATM o X.25, y operan en la capa de enlace de datos del modelo de
            referencia OSI. Dentro de la nube también es posible utilizar switches de red pública
            de telefonía conmutada para conexiones de conmutación de circuitos, por ejemplo
            red digital de servicios integrados o conexión telefónica analógica.<br/>
            • Router: proporciona puertos de interfaz de internetworking y acceso WAN que se
            utilizan para conectarse con la red del proveedor de servicios.<br/>
            • Router núcleo (core): router que reside en el centro o backbone de la WAN y no
            en la periferia. 
            </div>
        <footer className="pie">
            La informacion que se muestra a continucion fue obtenida de diversas fuentes de internet, por lo que no soy propietario de ella
        </footer>
    </div>

    <Link to="/Inicio"></Link>
    <Link to="/Tema2"></Link>
    <Link to="/Tema3"></Link>
    <Link to="/Tema4"></Link>
    <Link to="/Tema5"></Link>
    <Link to="/Tema6"></Link>
    <Switch>
        <Route path="/Tema2"> 
            <Tema2 />
        </Route>
        <Route path="/Inicio" >
            <Inicio />
        </Route>                
        <Route path="/Tema3">
            <Tema3 />
        </Route>
        <Route path="/Tema4">
            <Tema4 />
        </Route>
        <Route path="/Tema5">
            <Tema5 />
        </Route>
        <Route path="/Tema6">
            <Tema6 />
        </Route>
    </Switch>
        </Router>
    )
}

export default Tema1