import React from 'react'
import { Route } from 'react-router-dom'
import {BrowserRouter as Router, Switch, Link} from 'react-router-dom';
import Tema1 from './Tema1'; 

const Inicio = () =>{
    return (
        <Router>

        <div>
            <div className="cabecera">
        <h1>Unidad 3 Tecnologías WAN</h1>
        </div>
        <div className="cuerpo">
            <div className="unidad">
                <a href="/Tema1">
                    <p>3.1 Introduccion a redes WAN</p>
                    <img src="https://pc-solucion.es/wp-content/uploads/2018/03/red-WAN.jpg" alt="" />
                </a>
            </div>   
        <a href="/Tema2">
            <div className="unidad">
                <p>3.2 Elaces: dedicados y públicos</p>
                <img src="https://ccnadesdecero.es/wp-content/uploads/2018/01/Tecnolog%C3%ADas-de-acceso-WAN.png" alt="" />
            </div>
        </a>
        <a href="/Tema3">
            <div className="unidad">
                <p>3.3 Topologias WAN</p>
                <img src="https://4.bp.blogspot.com/_xr-9grCfdns/TAKKxhHBghI/AAAAAAAAABs/SAPCvRnjfek/w1200-h630-p-k-no-nu/topologias.png" alt="" />
            </div>
        </a>
        <a href="/Tema4">
            <div className="unidad">
                <p>3.4 Tecnologías WAN: PPP, XDLS, frame relay, ISDN, ATM</p>
                <img src="https://image.slidesharecdn.com/presentacin-datos-fijosmplstecnicacomercial-1227073687656342-9/95/presentacin-datos-fijos-mpls-tecnica-comercial-20-728.jpg?cb=1227067454" alt="" />
            </div>
        </a>
        <a href="/Tema5">
            <div className="unidad">
                <p>3.5 Configuración de dispotitivos WAN</p>
                <img src="https://2.bp.blogspot.com/-d9u_A1_Q4H8/UE9PSZRqMJI/AAAAAAAAAOA/Ag-oSeIuWCk/s1600/router.jpg" alt="" />
            </div>
        </a>
        <a href="/Tema6">
            <div className="unidad">
                <p>3.6 niveles de seguridad en redes VPN</p>
                <img src="https://inversorlatam.com/wp-content/uploads/2019/12/VPN-1.jpg" alt="" />
            </div>
        </a>
    </div>
    <footer className="pie">
        La informacion que se muestra a continucion fue obtenida de diversas fuentes de internet, por lo que no soy propietario de ella
    </footer>
        </div>
        <Switch>

        <Route path="/Tema1" exact>
            <Tema1 />
        </Route>
        </Switch>
        </Router>
    )
}

export default Inicio