import React, { Component } from 'react';
import { render } from 'react-dom';

import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom';
import Inicio from './components/Inicio'; 
import Tema1 from './components/Tema1'; 
import Tema2 from './components/Tema2'; 
import Tema3 from './components/Tema3'; 
import Tema4 from './components/Tema4'; 
import Tema5 from './components/Tema5'; 
import Tema6 from './components/Tema6'; 

class App extends Component {
render(){
    return (
    <Router>
        <div className="App">
            <h3><Link to="/Inicio">Ir a Inicio</Link></h3>
            <Link to="/Tema1"></Link>
            <Link to="/Tema2"></Link>
            <Link to="/Tema3"></Link>
            <Link to="/Tema4"></Link>
            <Link to="/Tema5"></Link>
            <Link to="/Tema6"></Link>
            <Switch>
                <Route path="/Tema1">
                    <Tema1 />
                </Route>
                <Route path="/Inicio" >
                    <Inicio />
                </Route>
                <Route path="/Tema2">
                    <Tema2 />
                </Route>
                <Route path="/Tema3">
                    <Tema3 />
                </Route>
                <Route path="/Tema4">
                    <Tema4 />
                </Route>
                <Route path="/Tema5">
                    <Tema5 />
                </Route>
                <Route path="/Tema6">
                    <Tema6 />
                </Route>
            </Switch>
        </div>
    </Router>
  );
    }
}

export default App;
