import React from 'react'

const Tema4 = () =>{
    return(
        <div>
            <div className="menus">
                <h1>3.4 Tecnologías WAN: PPP, XDLS, frame relay, ISDN, ATM</h1>
                <ul className="menu">
                    <li><a href="/Inicio">Menu principal</a></li>
                    <li><a href="/Tema1">3.1 Introduccion a redes WAN</a></li>
                    <li><a href="/Tema2">3.2 Elaces: dedicados y públicos</a></li>
                    <li><a href="/Tema3">3.3 Topologias WAN</a></li>
                    <li><a href="/Tema5">3.5 Configuración de dispotitivos WAN</a></li>
                    <li><a href="/Tema6">3.6 niveles de seguridad en redes VPN</a></li>
                </ul>
            </div>
            <div className="contenido">
                Point-to-Point Protocol (PPP)<br/><br/>
                El Protocolo punto a punto (PPP) ofrece un método estándar para transportar datagramas multiprotocolo a través 
                de enlaces de punto a punto. PPP está conformado por tres componentes principales:
                <br/>● Un método para encapsular datagramas multiprotocolo.
                <br/>● Un protocolo de control de enlace (LCP) para establecer, configurar y probar la conexión de enlace de datos.
                <br/>● Una familia de protocolos de control de red (NCP) para establecer y configurar distintos protocolos de capa de red.
                <br/><br/><br/>XDLS<br/><br/>Está formado por un conjunto de tecnologías que proveen un gran ancho de banda sobre circuitos locales de cable de cobre, sin 
                amplificadores ni repetidores de señal a lo largo de la ruta del cableado, entre la conexión del cliente y el primer nodo de la red.
                Son unas tecnologías de acceso punto a punto a través de la red pública, que permiten un flujo de información tanto simétrica como 
                asimétrica y de alta velocidad.
                <br/>Las tecnologías XDSL convierten las líneas analógicas convencionales en digitales de alta velocidad, con las que es posible ofrecer 
                servicios de banda ancha en el domicilio de los clientes.
                <br/>xDSL es una tecnología «Modem-Like» (muy  parecida a la tecnóloga de los modem), donde se requiere un dispositivo xDSL terminal en 
                cada extremo del circuito de cobre. Estos dispositivos aceptan flujo de datos, generalmente en formato digital, y lo sobreponen a 
                una señal análoga de alta velocidad. Las dos técnicas de modulación mas usadas actualmente para xDSL son:
                <br/>• CAP-Carrier-less Amplitude Phase Modulation
                <br/>• DMT-Discrete Multitone Modulation.
                <br/><br/><br/>Frame Relay<br/><br/>
                Frame Relay o (Frame-mode Bearer Service) es una técnica de comunicación mediante retransmisión de tramas para redes de circuito 
                virtual. 
                Consiste en una forma simplificada de tecnología de conmutación de paquetes que transmite una variedad de tamaños de tramas o
                marcos (“frames”) para datos, perfecto para la transmisión de grandes cantidades de datos. La técnica Frame Relay se utiliza 
                para un servicio de transmisión de voz y datos   a alta velocidad que permite la interconexión de redes de área local separadas 
                geográficamente a un coste menor.
                <img src="https://1.bp.blogspot.com/-sbzeAiTektI/XMuxGfPhKAI/AAAAAAAAAD0/_gF9bdHC8PwENbFU5RkLTSRM-lTSh815wCLcBGAs/s400/Frame_Relay.png" alt=""/>
                <br/>Frame Relay se ha convertido en la tecnología WAN más utilizada del mundo. Grandes empresas, gobiernos, ISP y pequeñas empresas 
                usan Frame Relay, principalmente a causa de su precio y flexibilidad.
                Frame Relay reduce los costos de redes a través del uso de menos equipos, menos complejidad y una implementación más fácil. 
                Aún más, Frame Relay proporciona un mayor ancho de banda, mejor fiabilidad y resistencia a fallas que las líneas privadas o 
                arrendadas
                Cuando cree una WAN, independientemente del transporte que elija, siempre hay un mínimo de tres componentes básicos o grupos 
                de componentes que se conectan en dos sitios. Cada sitio necesita su propio equipo (DTE) para acceder a la oficina central de 
                la empresa telefónica que presta servicios al área (DCE). El tercer componente se encuentra en el medio, y une los dos puntos 
                de acceso. En la figura, ésta es la parte proporcionada por el backbone de Frame Relay.
                <br/><br/><br/>ISDN<br/><br/>
                En que consiste esta tecnología ISDN es un complejo sistema de procesamiento de llamadas que permiten transportar por la red 
                telefónica voz y datos en el mismo "chorro" digital. ISDN es una tecnología totalmente digital que permite el transporte de 
                voz y de datos (textos, gráficas, videoconferencia, etc), todo transmitido desde una única interfaz de red. Las ventajas más 
                sobresalientes que tiene ISDN con respecto a las conexiones por modem conocidas por nosotros, son la velocidad y confiabilidad 
                de la conexión.<br/>
                Usando ISDN se pueden lograr conexiones a más de 64 kbps lo cual significa un aumento de más del 50% sobre la velocidad de 
                las conexiones típicas que tenemos con los módems actuales. Hay equipos ISDN en el mercado para todas las necesidades, equipos 
                para el hogar en los cuales se conecta un sólo micro y el teléfono, equipos para la pequeña oficina (SOHO), donde se utiliza una 
                misma conexión ISDN para comunicar varios estaciones de trabajo a una red remota, hasta llegar a equipos que soportan gran 
                cantidad de tráfico y numerosos esquemas de enrutamiento orientados a las grandes corporaciones.<br/> 
                Para lograr conectarse a este servidor la llamada deberá hacerse desde otra línea ISDN ya que el equipo adquirido no posee 
                módems sino terminal adapters en su totalidad Por esta razón el usuario que desee conectarse por este servidor debe hacer 
                cierta inversión, que consiste en adquirir una línea ISDN y un adaptador de terminal que hace las veces de modem.
                <img src="https://3.bp.blogspot.com/_qe-fFsEPo3c/TTfBaTeD3iI/AAAAAAAAABA/XqniMQbc9l4/s400/ISDN3.GIF" alt=""/>
                <br/><br/><br/>ATM<br/><br/>
                Con esta tecnología, a fin de aprovechar al máximo la capacidad de los sistemas de transmisión, sean estos de cable o 
                radioeléctricos, la información no es transmitida y conmutada a través de canales asignados en permanencia, sino en forma 
                de cortos paquetes (celdas ATM) de longitud constante y que pueden ser enrutadas individualmente mediante el uso de los 
                denominados canales virtuales ytrayectos virtuales. Son estructuras de datos de 53 bytes compuestas por dos campos principales: 
                1. Header, sus 5 bytes tienen tres funciones principales: identificación del canal, información para la detección de errores y 
                si la célula es o no utilizada.<br/> 
                Eventualmente puede contener también corrección de errores y un número de secuencia. 2. Payload, tiene 48 bytes fundamentalmente 
                con datos del usuario y protocolos AAL que también son considerados como datos del usuario.<br/>
                una ventaja de la tecnología ATM es la utilización eficiente del ancho de banda: por el mismo "canal" circulan celdas que pueden 
                llevar información de voz, datos o imagen y todas reciben el mismo tratamiento en los conmutadores.<br/>
                ATM es más adecuada para aplicaciones y sistemas con altos volúmenes de transmisión de datos de medios combinados, en particular: 
                datos, voz y audio.<br/>
                <img src="https://rdv-files.nyc3.cdn.digitaloceanspaces.com/pub/html/files_html/0/7/0/000050700.png" alt=""/>
            </div>
            <footer className="pie">
                La informacion que se muestra a continucion fue obtenida de diversas fuentes de internet, por lo que no soy propietario de ella
            </footer>
        </div>
    )
}

export default Tema4